<template>
  <section class="content">
    <div class="container">
      <div class="content__info">
        <p class="content__info-degree">{{ getTemp }}°<span>Today</span></p>
        <p class="content__info-time">Time: {{ getTime }}</p>
        <p class="content__info-city">City: {{ weatherDB?.name }}</p>
        <img :src="getIcon" alt="" class="content__info-icon" />
      </div>
      <div class="content__desc">
        <div class="content__desc-item">
          <img src="@/assets/images/temp.svg" alt="" />
          <span class="content__desc-span">Temperature</span>
          <p class="content__desc-txt">{{ getTemp }}° - feels like {{ getTempFeelsLike }}°</p>
        </div>
        <div class="content__desc-item">
          <img src="@/assets/images/davlenie.svg" alt="" />
          <span class="content__desc-span">Pressure</span>
          <p class="content__desc-txt">{{ weatherDB?.current?.pressure }} mm mercury column - normal</p>
        </div>
        <div class="content__desc-item">
          <img src="@/assets/images/osadki.svg" alt="" />
          <span class="content__desc-span">Precipitation</span>
          <p class="content__desc-txt">{{ weatherDB?.current?.clouds == 0 ? 'No Precipitation' : weatherDB?.current?.clouds + "%" }}</p>
        </div>
        <div class="content__desc-item">
          <img src="@/assets/images/wind.svg" alt="" />
          <span class="content__desc-span">Wind</span>
          <p class="content__desc-txt">{{ weatherDB?.current?.wind_speed }} m/s southwest - light wind</p>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="content__week">
        <Day v-for="(day, idx) in weatherDB?.daily.slice(1)" :key="idx" :day="day" />
      </div>
    </div>
  </section>
</template>

<script>
import { mapState } from "vuex";
import { weatherIcons } from "@/assets/js/icons";
import Day from "@/components/Day/Day.vue";
export default {
  name: "ContentView",
  computed: {
    ...mapState(["weatherDB"]),
    getTemp() {
      return Math.round(this.weatherDB?.current?.temp);
    },
    getTime() {
      return new Date().toLocaleString("en-US", {
        timeZone: this.weatherDB?.timezone,
        timeStyle: "short",
        hourCycle: "h23",
      });
    },
    getIcon() {
      return (
        weatherIcons[this.weatherDB?.current?.weather?.[0]?.description] ||
        weatherIcons["clear sky"]
      );
    },
    getTempFeelsLike() {
      return Math.round(this.weatherDB?.current?.feels_like);
    },
  },
  components: {
    Day,
  },
};
</script>

<style lang="scss" scoped>
</style>